* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

body {
font-family: Arial, Helvetica, sans-serif;
line-height: 1.4;
}

a {
color: #333;
text-decoration: none;
}

.container {
  padding: 0 1rem;
}

.btn {
  display: inline-block;
  border: none;
  background: #006367;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover {
  background: #017F84;
}